
/* ==================== common styles ======================= */

html {
  font-size: 10px;
  /*used for rem-units*/
  overflow-x: hidden;
}

body {
  min-width: 320px;
  overflow-x: hidden;
  
  font-family: $reg;
  font-size: 16px;
  line-height: 1.5;
  color: #191919;
  background-color: #f5f5f5;
  padding-bottom: 1000px;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.hidden {
  display: none;
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(50,0,0, .5);
  z-index: 5;
  &.transparent {
    background: rgba(10,0,100, .5);
  }
}



a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
}

button {
  font-family: inherit;
  font-size: inherit;
  background: none;
  cursor: pointer;
}

a,
button,
img,
input,
textarea {
  outline: none;
  border: none;
}

::-webkit-input-placeholder {
  color: #8b8b8b;
  font-size: 18px;
  opacity: 1;
  font-family: $reg;
}

::-moz-placeholder {
  color: #8b8b8b;
  font-size: 18px;
  opacity: 1;
  font-family: $reg;
}

:-ms-input-placeholder {
  color: #8b8b8b;
  font-size: 18px;
  opacity: 1;
  font-family: $reg;
}

::placeholder {
  color: #8b8b8b;
  font-size: 18px;
  opacity: 1;
  font-family: $reg;
}

section {
  position: relative;
  outline: 1px solid gold;
}

.wrapper {
  //position: relative;
  width: 96%;
  max-width: 1110px;
  margin: 0 auto;
  outline: 2px solid #00f;
}

h1,
h2 {
  font-family: $bold;
  font-size: 48px;
  line-height: 1.2;
  text-align: center;
  margin-bottom: 22px;
}

h1 {
  margin-bottom: 33px;
}

h3 {
  font-family: $bold;
  font-size: 36px;
}

.subheading {
  text-align: center;
  font-size: 18px;
  margin-bottom: 60px;
}

///*-------------------------- header --------------------------*/

.header {
  position: relative;
  padding: 18px 0 60px;
  background-color: $dark;
  color: #fff;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  //margin-bottom: 30px;
  >div {
    display: flex;
    align-items: center;
  }
}

.mobile-logo-cont {
  display: none;
  max-width: 200px;
}

nav a {
  display: inline-block;
  position: relative;
  //padding-bottom: 2px;
  &:after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 2px;
    background-color: $blue;
    transform: translateX(-50%);
    transition: all .4s;
  }
  &:hover,
  &.active {
    &:after {
      width: 100%;
    }
  }
}


.header-nav,
.social {
  li {
    display: inline-block;
  }
}
.header-nav {
  ul {
    white-space: nowrap;
  }
  li {
    margin-right: 35px;
    &:last-child {
      margin-right: 0;
    }
  }
}
.social {
  margin: 18px 78px 10px 20px;
  white-space: nowrap;
  li {
    margin-right: 7px;
    &:last-child {
      margin-right: 0;
    }
  }
  a {
    display: block;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    transition: all .4s;
    &:hover {
      box-shadow: 0 0 20px #fff;
    }
  }
}

.modal-call {
  display: inline-block;
  width: 255px;
  height: 45px;
  line-height: 41px;
  border-radius: 22.5px;
  border: 2px solid $blue;
  color: #fff;
  transition: all .4s;
  &:hover {
    background-color: $blue;
    color: #fff;
  }
}
.header-bottom {
  padding-top: 24px;
  line-height: 1.2;
}
.header-bottom,
.logo-cont,
.address-cont {
  display: flex;
  justify-content: space-between;
}

.logo-cont {
  max-width: 380px;
  a {
    display: block;
    flex: 0 0 156px;
    margin: 0 30px 15px 5px;
  }
  >p {
    align-self: center;
    font-family: $light;
    padding-bottom: 8px;
    letter-spacing: .6px;
    max-width: 200px;
  }
}


.address-item {
  padding-left: 34px;
  margin-right: 20px;
  position: relative;
  //max-width: 220px;
  &:before {
    content: "";
    display: block;
    width: 24px;
    height: 16px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../img/icons/metro.png);
    background-repeat: no-repeat;
  }
  >div:first-child {
    font-family: $bold;
    font-size: 18px;
    margin-bottom: 2px;
  }
}

.address-cont,
.phone-cont {
  padding-top: 15px;
}
.phone-cont {
  text-align: right;
  max-width: 270px;
  >a,
  >div {
    display: block;
    margin-bottom: 7px;
    white-space: nowrap;
  }
  >div {
    letter-spacing: .8px;
  }
  img {
    display: inline-block;
    margin-right: 5px;
    vertical-align: sub;
  }
}

.phone-link {
  font-family: $bold;
  font-size: 24px;
}
.header-dropdown-cont {
  position: absolute;
  bottom: -55px;
  left: 50%;
  transform: translateX(-50%);
  width: 96%;
  max-width: 1110px;
  margin: 0 auto;
  color: #191919;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 10px 30px rgba(0,0,0,.3);
  z-index: 6;
}
.tab-bar {

  display: flex;
  justify-content: space-between;
  position: relative;

  font-size: 16px;
  line-height: 1.2;
  width: 100%;
  
  >span {
    display: flex;
    align-items: center;
    font-family: $light;
    background-color: $blue;
    color: #fff;
    min-width: 240px;
    max-width: 240px;
    white-space: normal;
    padding: 10px 30px 10px 50px;
  }

}


.tab-list {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  >li {
    flex-grow: 1;
    text-align: center;
    position: relative;
    white-space: nowrap;
    font-family: $bold;
    min-height: 100px;
    background-color: #fff;
    cursor: pointer;
    transition: all .4s;
    &:last-child {
      border-radius: 0 4px 4px 0;
    }
    &:first-child>span {
      padding-left: 40px;
    }
    
    &:last-child>span {
      padding-right: 40px;
    }
    
    &.active {
      > span {
        position: relative;
        &:after {
          display: block;
        }
      }
    }
    >span,
    >a {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 10px;
      &:after {
        display: none;
        content: "";
        border: solid transparent;
        height: 0;
        width: 0;
        position: absolute;
        top: 85px;
        left: 50%;
        transform: translateX(-50%);
        pointer-events: none;
        border-bottom-color: #fff;
        border-width: 16px;
        border-bottom-width: 20px;
        z-index: -1;
      }
    }
  }
}

.dropdown {
  display: none;
  position: absolute;
  top: 120px;
  right: 0;
  color: #191919;
  text-align: left;
  background-color: #fff;
  padding: 18px 7px 16px 35px;
  border-radius: 4px;
  font-family: $reg;
  width: 96vw;
  max-width: 635px;
  
  >span {
    display: inline-block;
    font-family: $bold;
    margin-bottom: 14px;
    &:nth-child(1){
      width: 130px;
    }
    &:nth-child(2) {
      width: 70%;
      padding-left: 30px;
    }
  }
}

.tab-list {
  >li {
    &:nth-child(1),
    &:nth-child(2) {
      &:after {
        left: 65%;
      }
      .dropdown {
        right: auto;
        left: 40px;
      }
    }
  }
}
.drop-content {
  display: flex;
  justify-content: space-between;
  position: relative;
  height: 455px;
  max-height: 90vh;
  li {
    margin-bottom: 11px;
    a {
      display: inline-block;
      position: relative;
      &:after {
        content: "";
        display: block;
        width: 0;
        height: 2px;
        background-color: $blue;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        transition: all .4s;
      }
    }
    &.active,
    &:hover {
      a {
        &:after {
          width: 100%;
        }
      }
    }
  }
  &:after {
    content: "";
    display: block;
    width: 100%;
    height: 50px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(180deg, transparent, #fff 90%, #fff);
    z-index: 1;
  }
}

.brands-list-cont {
  min-width: 130px;
  max-width: 130px;
  max-height: 100%;
  overflow-x: hidden; 
  overflow-y: auto;
  white-space: normal;
}

.works-list-cont {
  flex-grow: 1;
  max-height: 100%;
  overflow-y: auto;
  white-space: normal;
  position: relative;

}

.tab {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  padding: 0 10px 0 30px;
}

// mobile
.btn-menu {
  position: fixed;
  top: 22px;
  right: 10px;
  width: 40px;
  height: 40px;
  background: rgba(102,153,226, .8);
  border: 1px solid #fff;
  border-radius: 5px;
  cursor: pointer;
  z-index: 3;
  display: none;
  span {
    display: block;
    background: #fff;
    position: absolute;
    top: 18px;
    left: 8px;
    right: 8px;
    height: 3px;
    border: none;
    border-radius: 2px;
    &:before,
    &:after {
      content: "";
      position: absolute;
      display: block;
      left: 0;
      width: 100%;
      height: 3px;
      background: #fff;
      border-radius: 2px;
    }
    &:before {
      top: -10px;
    }
    &:after {
      top: 10px;
    }
  }
}


.mob-cont {
  position: fixed;
  width: 320px;
  background-color: #fff;
  top: 0;
  bottom: 0;
  right: -350px;
  overflow-y: auto;
  padding: 20px 15px 20px 30px;
  -webkit-box-shadow: -2px 5px 14px 3px #333;
  box-shadow: -2px 5px 14px 3px #333;
  z-index: 9;
  color: #0a0a0a;
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
}

.mob-cont.active {
  right: 0;
}

.mob-cont li {
  display: block;
  margin-bottom: 30px;
  white-space: nowrap;
}

.mob-cont a {
  display: inline-block;
  position: relative;
  font-family: "Roboto-Bold", sans-serif;
  font-size: 20px;
  color: #191919;
  text-transform: uppercase;
  &:after {
    content: "";
    display: block;
    width: 0;
    height: 2px;
    background-color: $blue;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);

  }
  &.active {
    &:after {
      width: 100%;
    }
  }
}

.mob-top {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 35px;
}

.close {
  display: block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  //background-color: $blue;
  color: $blue;
  border-radius: 4px;
  text-align: center;
  font-size: 36px;
  cursor: pointer;
}


.breadcrumbs-cont {
  background-color: #fff;
  padding: 90px 0 10px;
}

.breadcrumbs {
  
  overflow: hidden;
  padding-bottom: 10px;
  text-align: left;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.breadcrumbs li {
  display: inline-block;
  white-space: nowrap;
  font-size: 18px;
  color: #bababa;
}

.breadcrumbs li:after {
  content: "/";
  margin-left: 5px;
}

.breadcrumbs li:last-child {
  color: #0a0a0a;
}

.breadcrumbs li:last-child:after {
  display: none;
}

.breadcrumbs a {
  display: inline-block;
  position: relative;
}

.breadcrumbs a:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 1px;
  background-color: $blue;
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
}

.breadcrumbs a:hover:after {
  width: 100%;
}


.quick-buttons-section {

  background-color: #e3e3e3;
  padding: 17rem 0 7rem;
  //min-height: 790px;
  overflow: hidden;
  >img {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    //height: 100%;
    max-width: none;
  }
  .wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
    
  }

}

.quick-btn {
  display: block;
  position: relative;
  flex: 1 1 100%;
  max-width: 48.6%;
  font-size: 24px;
  line-height: 1.2;
  color: #fff;
  border-radius: 4px;
  margin-bottom: 30px;
  overflow: hidden;
  transition: all .4s;
  >img {
    display: block;
    transition: all .4s;
  }
  >span {
    position: absolute;
    top: 18%;
    left: 12%;
    max-width: 250px;
  }
  &:hover {
    box-shadow: 2px 6px 20px rgba(0,0,0, .4);
    >img {
      transform: scale(1.04) translateX(-10px);
    }
  }
}

.advantages-section {
  padding: 9rem 0 16rem;
  background-color: #191919;
  color: #fff;
}

.advantages-cont {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.advantage {
  position: relative;
  flex: 1 1 100%;
  max-width: 48.6%;
  font-size: 24px;
  line-height: 1.2;
  border-radius: 4px;
  margin-bottom: 30px;
  background-color: #fff;
  color: #191919;
  min-height: 217px;
  overflow: hidden;
  transition: all .4s;
  p {
    font-size: 18px;
    line-height: 1.67;
    letter-spacing: .3px;
  }
  .adv-title {
    font-family: $black;
    font-size: 22px;
    line-height: 1;
    margin-bottom: 19px;
    letter-spacing: 0;
  }
}
.adv-content {
  position: relative;
  padding: 40px 30px 20px 130px;
  width: 100%;
  height: 100%;
  z-index: 1;
  overflow: hidden;
  &:before {
    content: "";
    display: block;
    width: 63px;
    height: 80px;
    position: absolute;
    top: 40px;
    left: 36px;
    background-image: url(../img/sprite/advSprite.png);
    background-repeat: no-repeat;
  }

}


