.news-section {
  padding: 9rem 0 16rem;
}

.news-cont {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 16px;
}

.news-prev {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1 1 500px;
  max-width: 31.5%;
  margin-bottom: 30px;
  transition: all .4s;
}

.news-img-cont {
  //position: relative;
  //height: 213px;
  width: 100%;
  overflow: hidden;

  img {
    display: block;
    margin: 0 auto;
    transition: all .4s;
  }
}
.news-text-cont {
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  flex-grow: 1;
  padding: 25px 0;
  letter-spacing: .3px;
  div {
    max-width: 100%;/*IE10 fix*/
  }

}
.news-title {
  font-family: $bold;
  font-size: 24px;
  line-height: 1.2;
  margin-bottom: 17px;
  letter-spacing: 0;
}

.more {
  display: inline-block;
  position: relative;
  font-family: $bold;
  font-size: 18px;
  text-transform: uppercase;
  color: #9e9e9e;
  padding-top: 18px;
  &:after {
    content: "";
    display: block;
    width: 0;
    height: 1px;
    background-color: #9e9e9e;
