    display: block;
    text-align: left;
    font-family: $black;
    font-size: 48px;
    color: #686868;
    margin-bottom: 2px;

  }
}

.step {
  flex: 1 1 100%;
  max-width: 350px;
  margin-bottom: 6rem;

  p:first-child {
    font-family: $bold;
    font-size: 24px;
    margin-bottom: 9px;
    letter-spacing: 0;
  }
  p {
    letter-spacing: .4px;
  }

