.os-bar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #fff;
  color: #191919;
  border-radius: 4px;
  padding: 38px 65px 18px;
  >div {
    flex: 1 1 100%;
    max-width: 30%;
    padding-left: 50px;
    position: relative;
    margin-bottom: 20px;
    &:before {
      content: "";
      display: block;
      width: 30px;
      height: 40px;
      position: absolute;
      top: -5px;
      left: 0;
      background-image: url(../img/sprite/OSSprite.png);
      background-repeat: no-repeat;
    }
    p:first-child {
      font-family: $bold;
      font-size: 24px;
      line-height: 1;
      color: #0a0a0a;
      margin-bottom: 6px;
      letter-spacing: 0;
    }
    p {
      letter-spacing: .4px;
    }
  }
  
}

.android:before {
  background-position: 0 -2px;
}

.ios:before {
  background-position: 0 -48px;
}

.windows:before {
  background-position: 0 -103px;
}

.about-section {
  padding: 8.5rem 0 4rem;
  .wrapper>img {
    width: 100%;
  }
  .more {
    padding-top: 25px;
    margin-bottom: 95px;
  }
}

.social-outer {
  text-align: center;
  .more {
    margin-bottom: 45px;
    &:after {
      width: 100%;
    }
  }
}

.columned {
  column-count: 2;
  //column-width: 450px;
  column-gap: 40px;
  padding-top: 5rem;
  margin-bottom: 10px;
  h3 {
    column-span: all;
    font-size: 25px;
    letter-spacing: -.5px;
    margin-bottom: 16px;
  }

}

.social-cont {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 10px 0;
  margin-bottom: 5.8rem;
  a {
    flex: 1 1 100%;
    max-width: 31.8%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 155px;
    background-color: #fff;
    color: $blue;
    font-family: $bold;
    font-size: 18px;
    border-radius: 7px;
    margin-bottom: 20px;
    transition: all .4s;
    span {
      white-space: nowrap;
      &:before {
        content: "";
        display: inline-block;
        width: 82px;
        height: 86px;
        background-image: url(../img/sprite/socialSprite.png);
        background-repeat: no-repeat;
        vertical-align: middle;
        margin-right: 25px;
        transition: all .4s;
